/* eslint-disable */

import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import './App.scss';

function Knowhowpg() {

    let state = useSelector((state) => state);
    let { id } = useParams();

    return (
        <div class="wrap">
            <header>
                <h1>{state.reducer4[id].title}</h1>
                <hr />
                <div>{state.reducer4[id].date} 작성 by {state.reducer4[id].writer}</div>
                <img class="main-img" src={state.reducer4[id].img} alt="코딩사진" />
                {
                    state.reducer4[id].p.map(function (a, i) {
                        return (
                            <div className="writing" key={i}>
                                <p>{state.reducer4[id].p[i].p}</p>
                            </div>
                        )
                    })
                }
            </header>
            <div class="comment-content">
                <h5>코멘트를 남겨주세요 :&#41;</h5>
                <br />
                <div className="write">
                    <textarea class="comment-box" placeholder="댓글을 남겨주세요"></textarea>
                    <button type="submit">등록</button>
                </div>
                {
                    state.reducer4[id].comments.map(function (a, i) {
                        return (
                            <div className="comment" key={i}>
                                <span>{state.reducer4[id].comments[i].writer}**</span>
                                <p>{state.reducer4[id].comments[i].comment}</p>
                            </div>
                        )
                    })
                }
            </div>
        </div>
    )
}

export default Knowhowpg;