import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import './App.scss';
import { Accordion } from "react-bootstrap";

function MentoDetail1() {

    let state = useSelector((state) => state);

    let { id } = useParams();

    return (
        <div className="mento-detail">
            <div className="profile">
                <div className="img"><img src={state.reducer2[0].mentoList1[id].img} alt="" /></div>
                <div className="card">
                    <div className="namediv">
                        <span className="name">{state.reducer2[0].mentoList1[id].name} 멘토</span>
                        <span className="feature badge bg-secondary">{state.reducer2[0].mentoList1[id].feature}</span>
                    </div>
                    <hr />
                    <p className="ment">{state.reducer2[0].mentoList1[id].ment}</p>
                    <div className="number">
                        <span>스터디 인원</span>
                        <span className="num">{state.reducer2[0].mentoList1[id].num} 명</span>
                    </div>
                    <hr />
                    <span className="message">멘토에게 메세지를 보내보세요</span>
                    <div className="button">
                        <button className="send btn-dark btn-lg">메세지 보내기</button>
                        <button className="call btn-danger btn-lg">멘토 요청하기</button>
                    </div>
                </div>
            </div>
            <div className="container">
                <span className="title">멘토에게 알려주세요</span>
                <div className="box">
                    <div className="percent">
                        <span>완료 시 멘토가 당신을 알 수 있어요</span>
                        <div className="bar"><div className="fill">64%</div></div>
                    </div>
                    <span className="question">멘토를 찾는 이유는 무엇인가요?</span>
                    <div className="reason">
                        <ul>
                            <li><input type="radio" id='reason1' name="reason" /><label htmlFor="reason1">같이 축구할 친구를 찾고 싶어요</label></li>
                            <li><input type="radio" id='reason2' name="reason" /><label htmlFor="reason2">여자친구가 축구를 좋아해요</label></li>
                            <li><input type="radio" id='reason3' name="reason" /><label htmlFor="reason3">운동하고 싶어요</label></li>
                            <li><input type="radio" id='reason4' name="reason" /><label htmlFor="reason4">전문가에게 배우고 싶어요</label></li>
                            <li><input type="radio" id='reason5' name="reason" /><label htmlFor="reason5">새로운 취미를 배우고 싶어요</label></li>
                        </ul>
                        <button>완료</button>
                    </div>
                </div>
            </div>
            <hr />
            <div class="container2">
                <h2 class="text-center">자주 묻는 질문</h2>
                <Accordion className="faq">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>Q. 멘토가 매칭이 안돼요</Accordion.Header>
                        <Accordion.Body>
                            멘토가 바쁘신가봐요..ㅜ 조금만 더 기다리시고 메세지를 보내보는건
                            어떨까요?
                        </Accordion.Body>
                    </Accordion.Item>
                    <Accordion.Item eventKey="1">
                        <Accordion.Header>Q. 멘토의 연락처를 알 수 있을까요?</Accordion.Header>
                        <Accordion.Body>
                            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod
                            tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim
                            veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea
                            commodo consequat. Duis aute irure dolor in reprehenderit in voluptate
                            velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat
                            cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id
                            est laborum.
                        </Accordion.Body>
                    </Accordion.Item>
                    <Accordion.Item eventKey="2">
                        <Accordion.Header>Q. 무엇을 배워야할까요?</Accordion.Header>
                        <Accordion.Body>
                            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod
                            tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim
                            veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea
                            commodo consequat. Duis aute irure dolor in reprehenderit in voluptate
                            velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat
                            cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id
                            est laborum.
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </div>
        </div>
    )
}

export default MentoDetail1;