/* eslint-disable */

import './App.scss';
import { Link, Route, Switch } from "react-router-dom";
import { useEffect, useState } from 'react';
import { Formik } from "formik";

function SignUp() {

    const initialValues = {// 각 양식 필드의 초기 값을 설명하는 객체
        // 각 키에 주어진 이름은 Formik에서 감시 할 입력 필드의 이름 값과 일치해야한다
        name: "",
        email: "",
        pw: "",
        repw: ""
    };

    // 데이터 폼 유효성 검사를 처리하는 함수를 받는다. 데이터 값 형식의 객체를 인수로 받아들이고 정의 된 규칙에 따라 객체의 각 속성의 유효성을 검사
    const validate = (values) => {
        // 값 객체의 각 키는 입력 필드의 이름과 일치해야한다
        let errors = {};

        const nameRegex = /^[가-힣]{2,6}$/;
        const emailRegex = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
        const pwRegex = /^(?=.*\d)(?=.*[a-zA-Z])[0-9a-zA-Z]{8,16}$/;

        //이름 값이 없을 경우
        if (!values.name) {
            errors.name = "이름을 입력해주세요";
            //정규식에 어긋나는 경우
        } else if (!nameRegex.test(values.name)) {
            errors.name = "유효하지 않은 이름 입니다.";
        }

        //이메일 값이 없을 경우
        if (!values.email) {
            errors.email = "이메일을 입력해주세요";
            //정규식에 어긋나는 경우
        } else if (!emailRegex.test(values.email)) {
            errors.email = "양식에 맞지 않는 이메일 입니다";
        }

        //비밀번호 값이 없을 경우
        if (!values.pw) {
            errors.pw = "비밀번호를 입력해주세요";
            //비밀번호 길이가 4글자보다 작을 경우
        } else if (!pwRegex.test(values.pw)) {
            errors.pw = "양식에 맞지 않는 비밀번호입니다";
        }

        //비밀번호 값이 없을 경우
        if (!values.repw) {
            errors.repw = "비밀번호를 입력해주세요";
            //비밀번호 길이가 4글자보다 작을 경우
        } else if (values.repw != values.pw) {
            errors.repw = "비밀번호가 일치하지 않습니다";
        }

        return errors;
    };

    // submitForm : 폼 데이터의 제출을 처리한다
    const submitForm = (values) => {
        console.log(values);
    };

    return (

        // initialValues : 각 양식 필드의 초기 값을 설명하는 객체
        // validate : 데이터 폼 유효성 검사를 처리하는 함수를 받음
        // onSubmit : 사용자가 제출 한 후 발생하는 작업을 처리
        // 💡 값 객체의 각 키는 입력 필드의 이름과 일치해야합니다.
        <Formik initialValues={initialValues} validate={validate} onSubmit={submitForm}>
            {(formik) => { //💡 formik props
                //💡 formik의 render API 속성들 입니다.
                const { values, handleChange, handleSubmit, errors, touched, handleBlur, isValid, dirty } = formik;
                return (
                    <div className="signup">
                        <div className="background">
                            <div className="frame">
                                <div className="logIn">
                                    <div className="title">
                                        <span className="mento">MENTO</span>
                                        <span className="find">FIND YOUR MENTOR</span>
                                    </div>
                                    <div className="btn">
                                        <Link to="/signin"><button>로그인</button></Link>
                                    </div>
                                </div>
                                <div className="signUp">
                                    <div className="title">회원가입</div>
                                    {/* form */}
                                    <form onSubmit={handleSubmit} action="#" className="signUp">
                                        <div className="radio">
                                            <input type="radio" name="kind" id="radio1" required checked /><label for="radio1">멘티로 가입</label>
                                            <input type="radio" name="kind" id="radio2" required /><label for="radio2">멘토로 가입</label>
                                        </div>
                                        <div className="text">
                                            <label htmlFor="name">이름(실명)</label>
                                            <input type="text" name="name" id="name" value={values.name} onChange={handleChange} onBlur={handleBlur}
                                                className={errors.name && touched.name ? "input-error" : null} placeholder="이름(실명)을 입력해주세요" minlength="2" maxlength="6" />
                                            {/* 이름 에러나 이메일 터치했을 때 span 실행 */}
                                            {errors.name && touched.name && (<span className="error">{errors.name}</span>)}

                                        </div>
                                        <div className="text">
                                            <label htmlFor="email">이메일(Email)</label>
                                            <input type="email" name="email" id="email" value={values.email} onChange={handleChange} onBlur={handleBlur}
                                                className={errors.email && touched.email ? "input-error" : null} placeholder="이메일(example@mento.com)을 입력해주세요" />
                                            {/* 이메일 에러나 이메일 터치했을 때 span 실행 */}
                                            {errors.email && touched.email && (<span className="error">{errors.email}</span>)}

                                        </div>
                                        <div className="text">
                                            <label htmlFor="pw">비밀번호</label>
                                            <input type="password" name="pw" id="pw" value={values.pw} onChange={handleChange} onBlur={handleBlur}
                                                className={errors.pw && touched.pw ? "input-error" : null} placeholder="비밀번호(영문+숫자 조합 8~16자리) 입력해주세요" minlength="8" maxlength="16" />
                                            {/* 비밀번호 에러나 비밀번호 터치했을 때 span 실행 */}
                                            {errors.pw && touched.pw && (<span className="error">{errors.pw}</span>)}

                                        </div>
                                        <div className="text">
                                            <label htmlFor="repw">비밀번호 확인</label>
                                            <input type="password" name="repw" id="repw" value={values.repw} onChange={handleChange} onBlur={handleBlur}
                                                className={errors.repw && touched.repw ? "input-error" : null} placeholder="비밀번호(영문+숫자 조합 8~16자리) 입력해주세요" minlength="8" maxlength="16" />
                                            {/* 비밀번호 에러나 비밀번호 터치했을 때 span 실행 */}
                                            {errors.repw && touched.repw && (<span className="error">{errors.repw}</span>)}

                                        </div>
                                        <div className="checkbox">
                                            <div className="cb">
                                                <input type="checkbox" name="cb1" id="cb1" required /><label for="cb1">이용약관, 개인정보 수집 및 이용 동의 (필수)</label>
                                            </div>
                                            <div className="cb">
                                                <input type="checkbox" name="cb2" id="cb2" required /><label for="cb2">만 14세 이상 (필수)</label>
                                            </div>
                                        </div>
                                        <div className="btn">
                                            <input type="submit" className={dirty && isValid ? "" : "disabled-btn"} value="회원가입" />
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            }}
        </Formik>
    );
};
// }

export default SignUp;