/* eslint-disable */

import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { Link, Route, Switch } from "react-router-dom";
import './App.scss';

function MentoSearch() {

    let state = useSelector((state) => state);
    let history = useHistory();

    return (
        <div className="mento-search">
            <header class="mento-search">
                <div class="header">
                    <div class="main-title">
                        <div class="title_1">멘토를 찾아보세요</div>
                    </div>
                    <div class="search">
                        <div class="text">나에게 맞는 멘토 찾기</div>
                        <form class="d-flex">
                            <input type="search" placeholder="Search" />
                            <button type="submit">Search</button>
                        </form>
                    </div>
                </div>
            </header>
            <div className="container">
                <h1>N명의 멘토</h1>
                <div className="contents">
                    {
                        state.reducer2[0].mentoList0.map(function (a, i) {
                            return (
                                <div class="card" key={i} onClick={() => { history.push("/mentodetail0/" + state.reducer2[0].mentoList0[i].id) }}>
                                    <div className="imgback"><img src={state.reducer2[0].mentoList0[i].img} class="card-img-top" alt="" /></div>
                                    <div class="card-body">
                                        <h5 class="card-title">{state.reducer2[0].mentoList0[i].name}</h5>
                                        <p class="card-text">{state.reducer2[0].mentoList0[i].job}</p>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
                <div className="contents">
                    {
                        state.reducer2[0].mentoList1.map(function (a, i) {
                            return (
                                <div class="card" key={i} onClick={() => { history.push("/mentodetail1/" + state.reducer2[0].mentoList1[i].id) }}>
                                    <div className="imgback"><img src={state.reducer2[0].mentoList1[i].img} class="card-img-top" alt="" /></div>
                                    <div class="card-body">
                                        <h5 class="card-title">{state.reducer2[0].mentoList1[i].name}</h5>
                                        <p class="card-text">{state.reducer2[0].mentoList1[i].job}</p>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
            <div className="container">
                <h1>멘토 인기 서비스</h1>
                <div className="contents">
                    {
                        state.reducer2[0].mentoList0.map(function (a, i) {
                            return (
                                <div class="content" key={i}>
                                    <div className="imgback"><img src={state.reducer2[0].mentoList0[i].img} class="content-img" alt="" /></div>
                                    <div class="content-body">
                                        <h5 class="content-title">{state.reducer2[0].mentoList0[i].name}</h5>
                                        <p class="content-text">{state.reducer2[0].mentoList0[i].job}</p>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        </div>
    )
}

export default MentoSearch;