/* eslint-disable */

import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { CSSTransition } from "react-transition-group";
import './App.scss';

function GroupSearch() {

    let state = useSelector((state) => state);
    let history = useHistory();
    let dispatch = useDispatch();
    let [스위치, 스위치변경] = useState(false);

    return (
        <div className="group-search">
            <header class="group-header">
                <div class="header">
                    <div class="main-title">
                        <div class="title_1">멘토 및 그룹찾기</div>
                    </div>
                    <div class="search">
                        <div class="text">나에게 맞는 멘토 및 그룹 찾기</div>
                        <form class="d-flex">
                            <input type="search" placeholder="Search" />
                            <button type="submit">Search</button>
                        </form>
                    </div>
                </div>
            </header>
            <section>
                <div className="button">
                    <button onClick={() => { dispatch({ type: "지역" }); 스위치변경(false) }}>지역</button>
                    <button onClick={() => { dispatch({ type: "카테고리" }); 스위치변경(false) }}>카테고리</button>
                </div>
                <div>
                    <CSSTransition in={스위치} classNames="wow" timeout={500}>
                        <TabContent 스위치변경={스위치변경} />
                    </CSSTransition>
                </div>
                <div className="contents">
                    {
                        state.reducer3.map(function (a, i) {
                            return (
                                <div className="content" key={i} onClick={() => { history.push("/groupdetail/" + state.reducer3[i].id) }}>
                                    <img src={state.reducer3[i].img} alt="" />
                                    <div className="content-body">
                                        <span className="title">{state.reducer3[i].title}</span>
                                        <span className="sentence">{state.reducer3[i].sentence}</span>
                                        <div className="grade">
                                            <span>⭐{state.reducer3[i].star}</span>
                                            <span>{state.reducer3[i].connectionNum}회 연결</span>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }

                </div>
            </section>
        </div>
    )
    // }
}

function TabContent(props) {

    let state = useSelector((state) => state);
    let dispatch = useDispatch();

    useEffect(() => {
        props.스위치변경(true);
    })

    if (state.reducer5[0].tab === 1) {
        return (
            <div className='tabContent'>
                <ul>
                    <li>전국</li>
                    <li>서울</li>
                    <li>경기</li>
                    <li>인천</li>
                    <li>대전</li>
                    <li>대구</li>
                    <li>광주</li>
                    <li>부산</li>
                    <li>울산</li>
                    <li>경남</li>
                    <li>경북</li>
                    <li>전남</li>
                    <li>전북</li>
                    <li>충남</li>
                    <li>충북</li>
                    <li>제주</li>
                    <li>강원</li>
                </ul>
                <button onClick={() => { dispatch({ type: "닫기" }) }}>닫기</button>
            </div>
        )
    } else if (state.reducer5[0].tab === 2) {
        return (
            <div className='tabContent'>
                <ul>
                    <li>레슨</li>
                    <li>홈/ 리빙</li>
                    <li>이벤트</li>
                    <li>비즈니스</li>
                    <li>디자인</li>
                    <li>외국어</li>
                    <li>스포츠</li>
                    <li>건강/ 미용</li>
                    <li>기타</li>
                </ul>
                <button onClick={() => { dispatch({ type: "닫기" }) }}>닫기</button>
            </div>
        )
    } else {
        return (
            <div></div>
        )
    }
}

export default GroupSearch;