import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import './App.scss';

function GroupDetail() {

    let state = useSelector((state) => state);
    let { id } = useParams();

    return (
        <div className="group-detail">
            <section>
                <div className="content">
                    <img src={state.reducer3[id].img} alt="" />
                    <div className="content-body">
                        <span className="title">{state.reducer3[id].title}</span>
                        <span className="sentence">{state.reducer3[id].sentence}</span>
                        <div className="grade">
                            <span>⭐{state.reducer3[id].star}</span>
                            <span>{state.reducer3[id].connectionNum}회 연결</span>
                        </div>
                    </div>
                </div>
                <hr />
                <div className="writer">
                    <span>{state.reducer3[id].writer}</span>
                    <p>{state.reducer3[id].introduction}</p>
                </div>
                <div className="info">
                    <div className="block">
                        <h4>한줄 소개</h4>
                        {
                            state.reducer3[id].한줄소개.map(function (a, i) {
                                return (
                                    <span key={i}>{state.reducer3[id].한줄소개[i].소개}</span>
                                )
                            })
                        }
                    </div>
                    <div className="block">
                        <h4>멘토 분야</h4>
                        {
                            state.reducer3[id].멘토분야.map(function (a, i) {
                                return (
                                    <span key={i}>{state.reducer3[id].멘토분야[i].field}</span>
                                )
                            })
                        }
                    </div>
                    <div className="block">
                        <h4>기본 정보</h4>
                        <span>{state.reducer3[id].본인인증}</span>
                        <span>🌍{state.reducer3[id].주소}</span>
                        <div className="call">
                            <span>📞연락 가능 시간: &nbsp;
                                <span>{state.reducer3[id].연락시작}</span>
                                &nbsp;- <span>{state.reducer3[id].연락마감}</span>
                            </span>
                        </div>
                    </div>
                    <div className="block">
                        <h4>추가 정보</h4>
                        <span>📃경력 {state.reducer3[id].경력}</span>
                        <span>👨‍👨‍👦‍👦함께한 분 {state.reducer3[id].함께한분}명</span>
                        <p>⚡ 슬랙 질문방 운영 <br />
                            언제든지 질문 가능한 슬랙 질문방 운영<br />
                            멘토의 빠른 답변 대기중</p>
                        <p>⚡ 꼼꼼한 자료<br />
                            초보자가 이해하기 쉽도록 구성된 자세한 자료</p>
                    </div>
                    <div className="block">
                        <h4>사진 및 동영상</h4>
                        {
                            state.reducer3[id].photo.map(function (a, i) {
                                return (
                                    <img key={i} src={state.reducer3[id].photo[i].img} alt="" />
                                )
                            })
                        }
                    </div>
                </div>
                <div className="review">
                    <div className="title">Review</div>
                    <span className="star">⭐{state.reducer3[id].star}</span>
                    <hr />
                    {
                        state.reducer3[id].review.map(function (a, i) {
                            return (
                                <div className="review-content" key={i}>
                                    <span className="name">{state.reducer3[id].review[i].name}**</span>
                                    <p>{state.reducer3[id].review[i].p}</p>
                                </div>
                            )
                        })
                    }
                </div>
            </section>
            <div class="request">
                <span class="req"> 신청하기</span>
            </div>
        </div>
    )
}

export default GroupDetail;