/* eslint-disable */

import { useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import { Link, Route, Switch } from "react-router-dom";
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import './App.scss';

function Home() {

    let state = useSelector((state) => state);
    let history = useHistory();

    let [nowPic, nowPicChange] = useState(1);
    let contents1 = useRef();
    let contents2 = useRef();
    function next1() {
        if (nowPic == 1) {
            contents1.current.style.transition = "transform 1s";
            contents1.current.style.transform = "translateX(-870px)";// -290*3
            return (nowPicChange(4));
        } else if (nowPic == 4) {
            contents1.current.style.transition = "transform 1s";
            contents1.current.style.transform = "translateX(-1160px)";// -290*4
            return (nowPicChange(7));
        }
    };
    function before1() {
        if (nowPic == 7) {
            contents1.current.style.transition = "transform 1s";
            contents1.current.style.transform = "translateX(-290px)";// -290*3
            return (nowPicChange(4));
        } else if (nowPic == 4) {
            contents1.current.style.transition = "transform 1s";
            contents1.current.style.transform = "translateX(0px)";
            return (nowPicChange(1));
        }
    };
    function next2() {
        if (nowPic == 1) {
            contents2.current.style.transition = "transform 1s";
            contents2.current.style.transform = "translateX(-870px)";// -290*3
            return (nowPicChange(4));
        } else if (nowPic == 4) {
            contents2.current.style.transition = "transform 1s";
            contents2.current.style.transform = "translateX(-1160px)";// -290*4
            return (nowPicChange(7));
        }
    };
    function before2() {
        if (nowPic == 7) {
            contents2.current.style.transition = "transform 1s";
            contents2.current.style.transform = "translateX(-290px)";// -290*3
            return (nowPicChange(4));
        } else if (nowPic == 4) {
            contents2.current.style.transition = "transform 1s";
            contents2.current.style.transform = "translateX(0px)";
            return (nowPicChange(1));
        }
    };
    return (
        <div className="home">
            <div className="header">
                <div className="main-title">
                    <div className="mento">MENTO</div>
                    <div className="find">FIND&nbsp;YOUR&nbsp;MENTOR</div>
                </div>
                {/* search */}
                <div className="search">
                    <div className="text">어떤 분야의 멘토를 찾으시나요?</div>
                    <form>
                        <input type="search" placeholder="Search" />
                        <button type="submit">Search</button>
                    </form>
                </div>
            </div>

            <div className="category_icons">
                <div className="category_icons4">
                    <div className="category">
                        <Link to="/mentosearch">
                            <div className="icon"><i className="fas fa-chalkboard-teacher"></i></div>
                            <span>레슨</span>
                        </Link>
                    </div>
                    <div className="category">
                        <a href="#">
                            <div className="icon"><i className="fas fa-home"></i></div>
                            <span>홈/리빙</span>
                        </a>
                    </div>
                    <div className="category">
                        <a href="#">
                            <div className="icon"><i className="fas fa-gift"></i></div>
                            <span>이벤트</span>
                        </a>
                    </div>
                    <div class="category">
                        <a href="#">
                            <div className="icon"><i className="fas fa-briefcase"></i></div>
                            <span>비즈니스</span>
                        </a>
                    </div>
                </div>
                <div className="category_icons4">
                    <div className="category">
                        <a href="#">
                            <div className="icon"><i className="fas fa-pencil-ruler"></i></div>
                            <span>디자인</span>
                        </a>
                    </div>
                    <div className="category">
                        <a href="#">
                            <div className="icon"><i className="fas fa-heartbeat"></i></div>
                            <span>건강/미용</span>
                        </a>
                    </div>
                    <div className="category">
                        <a href="#">
                            <div className="icon"><i className="fas fa-motorcycle"></i></div>
                            <span>알바</span>
                        </a>
                    </div>
                    <div className="category">
                        <a href="#">
                            <div className="icon"><i className="fas fa-ellipsis-h"></i></div>
                            <span>기타</span>
                        </a>
                    </div>
                </div>
            </div>

            <div className="knowhow">
                <div className="title">{state.reducer4[0].mainTitle}</div>
                <div className="btnWrap">
                    <div className="contentsWrap">
                        <div className="contents" ref={contents1}>
                            {
                                state.reducer4[0].content.map(function (a, i) {
                                    return (
                                        <div className="content" key={i} onClick={()=>{history.push("/knowhowpg/" + state.reducer4[0].content.id)}}>
                                            
                                                <img className="content_img" src={state.reducer4[0].content[i].img} alt="" />
                                                <span className="content_title">{state.reducer4[0].content[i].title}</span>
                                                <span className="content_name">{state.reducer4[0].content[i].writer}멘토</span>
                                            
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                    <button className="before" onClick={before1}><i className="fas fa-angle-left"></i></button>
                    <button className="next" onClick={next1}><i className="fas fa-angle-right"></i></button>
                </div>
            </div>

            <div className="magazine">
                <div className="title">{state.reducer[0].title}</div>
                <div className="btnWrap">
                    <div className="contentsWrap">
                        <div className="contents" ref={contents2}>
                            {
                                state.reducer[0].content.map(function (a, i) {
                                    return (
                                        <div className="content" key={i}>
                                            <a href={state.reducer[0].content[i].href}>
                                                <img className="content_img" src={state.reducer[0].content[i].img} alt="" />
                                                <span className="content_title">{state.reducer[0].content[i].title}</span>
                                            </a>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                    <button className="before" onClick={before2}><i className="fas fa-angle-left"></i></button>
                    <button className="next" onClick={next2}><i className="fas fa-angle-right"></i></button>
                </div>
            </div>

            <div className="map">
                <div className="cities">
                    <span className="title">전국 멘토 지도</span>

                    <span className="text"><i className="fas fa-thumbtack"></i>믿을 수 있는 전문가를 내가 사는
                        지역에서 찾아보세요.</span>

                    <ul className="city">
                        <a href="http://localhost:8083/">
                            <li>서울</li>
                        </a>
                        <a href="#">
                            <li>세종</li>
                        </a>
                        <a href="http://localhost:8083/">
                            <li>강원</li>
                        </a>
                        <a href="#">
                            <li>인천</li>
                        </a>
                        <a href="http://localhost:8083/">
                            <li>경기</li>
                        </a>
                        <a href="#">
                            <li>충북</li>
                        </a>
                        <a href="http://localhost:8083/">
                            <li>충남</li>
                        </a>
                        <a href="#">
                            <li>경북</li>
                        </a>
                        <a href="http://localhost:8083/">
                            <li>대전</li>
                        </a>
                        <a href="#">
                            <li>대구</li>
                        </a>
                        <a href="http://localhost:8083/">
                            <li>전북</li>
                        </a>
                        <a href="#">
                            <li>경남</li>
                        </a>
                        <a href="http://localhost:8083/">
                            <li>울산</li>
                        </a>
                        <a href="#">
                            <li>광주</li>
                        </a>
                        <a href="http://localhost:8083/">
                            <li>부산</li>
                        </a>
                        <a href="#">
                            <li>전남</li>
                        </a>
                        <a href="http://localhost:8083/">
                            <li>제주</li>
                        </a>
                    </ul>
                </div>
            </div>
        </div>


    )
}

export default Home;