let tab = [{ tab: 0 }]

function reducer(state = contentData, action) {
  return state;
};

function reducer2(state = mentoSearch, action) {
  return state;
};

function reducer3(state = groupSearch, action) {
  return state;
};

function reducer4(state = knowhowpg, action) {
  return state;
};

function reducer5(state = tab, action) {
  if (action.type === "지역") {
    let copy = [...state];
    copy[0].tab = 1;
    return copy;
  } else if (action.type === "카테고리") {
    let copy = [...state];
    copy[0].tab = 2;
    return copy;
  } else if (action.type === "닫기") {
    let copy = [...state];
    copy[0].tab = 0;
    return copy;
  } else {
    return state;
  }
};

let store = createStore(combineReducers({ reducer, reducer2, reducer3, reducer4, reducer5 }));

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

